import { Avatar } from '@mui/material';
import React from 'react';
import './jobpost.css';
import CallIcon from '@mui/icons-material/Call';
import EmailIcon from '@mui/icons-material/Email';

function Jobpost() {
  return (
    <div className='job_post'>
        <div className="post_head">
        <div className="user">
            <Avatar />
            <h5>username</h5>
        </div>
        <div className="timestamp">
            <small>timestamp</small>
        </div>
        </div>

        <div className="body_content">
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Repellendus voluptatibus neque nobis! Fugiat quaerat omnis repudiandae dicta accusantium harum eveniet sit reiciendis, quas facere nemo facilis voluptas est. </p>
        </div>

        <div className="post_footer">
            <div className="pay">
                <h5>Pay: Amount</h5>
            </div>
            <div className="contact_num">
                <CallIcon />
                <h5>: number</h5>
            </div>
            <div className="email">
                <EmailIcon />
                <h5>: email</h5>
            </div>

            <div className="address">
                <h5>Address: address</h5>
            </div>
        </div>
    </div>
  )
}

export default Jobpost