import { Avatar } from '@mui/material';
import React from 'react';
import './problempost.css';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';

function Problempost() {
  return (
    <div className='problem_post'>
        <div className="problem_head">
            <div className="user">
                <Avatar />
                <h5>username</h5>
            </div>
            <div className="timestamp">
                <small>timestamp</small>
            </div>
        </div>

        <div className="problem_body">
            <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Suscipit iusto facilis et aliquam hic repellat ratione, maxime ad tenetur aspernatur quisquam! Aliquid libero, nostrum nobis id illum, eaque earum cumque qui sint dignissimos architecto.</p>
        </div>

        <div className="problem_footer">
            <div className="vote">
            <VolunteerActivismIcon />
            </div>
            <div className="comment">
                <ChatBubbleOutlineIcon />
            </div>
        
        </div>

    </div>
  )
}

export default Problempost