import React, { useState } from 'react';
import {Avatar, Button } from '@mui/material';
import './infodiv.css';
import 'react-responsive-modal/styles.css';
import 'react-quill/dist/quill.snow.css';
import Modal from 'react-responsive-modal';
import { Close} from '@mui/icons-material';
import ReactQuill from 'react-quill';

function Infodiv() {

  const[isModalOpen, setisModalOpen] = useState(false);
const [imgUrl, setimgUrl]=useState((""));
const [post,setPost] = useState("");

const close=(<Close />)

  return (
    <div className='infodiv'>
        <div className="content">
            <h2>Wanna share your success journey</h2>
        </div>
        <div className="btn">
            <Button style={{color:"white"}} onClick={()=> setisModalOpen(true)}>write blog</Button>
            <Modal
        open = {isModalOpen}
        closeIcon = {close}
        onClose = {()=> setisModalOpen(false)}
        closeOnEsc
        centercloseOnOverlayClick={false}
        styles={{
          width:"70%",
          overlay: {
            height:"auto",

          }
        }}
        >
          <div className="post_content">

         
            <div className="blogpost">
                <ReactQuill /*  value={post}
                onChange={handleQuill} */placeholder="Enter your Answer" style={{height:"300px", paddingTop:"10px",}}/>
            </div>
            <div className="image" style={{paddingTop:"50px", }}>
            <input 
              value = {imgUrl}
              onChange = {(e) => setimgUrl(e.target.value)}
              style={{
                margin: "5px 0",
                border:"1px solid lightgray",
                padding:"10px",
                outlint:"2",
                width:"100%",
              }}
              type="text" placeholder='optional:place a link of image if needed' />
              {
                imgUrl !=="" &&  <img 
                style={{
                  height:"40vh",
                  objectFit:"contain",
                }} src={imgUrl} alt='image' />
              }
            </div>
            </div>
            <div className="modal_btn">
            <button className='cancel' onClick={()=>setisModalOpen(false)}>
              Cancel
            </button>
            <button /* onClick={handleSubmit} */type='submit' className='add'>
              Add{"  "}Post
            </button>
            </div>
        </Modal>
        </div>
    </div>
  )
}

export default Infodiv