import {Avatar, Button } from '@mui/material';
import React from 'react';
import './CSS/header.css';

function Header({pg, setPg}) {
  return (
    <div className='header'>
        <div className="logo">
            <img src="" alt="" />
            <h2>logo</h2>
        </div>
        <div className="navbar">
            <div className="pages">
            <Button style={{color:"white"}} onClick={()=>setPg('home')}>Home</Button>
            </div>
            <div className="pages">
            <Button style={{color:"white"}}
            onClick={()=>setPg('page2')}>page2</Button>
            </div>
            <div className="pages">
            <Button style={{color:"white"}} onClick={()=>setPg('page3')}>page3</Button>
            </div>
            <div className="pages">
            <Button style={{color:"white"}} onClick={()=>setPg('page4')}><a href=''> page4</a></Button>
            </div>
            <div className="pages profile">
            <Avatar />
            </div>
            
        </div>
    </div>
  )
}

export default Header