import { Button } from '@mui/material';
import React, { useState } from 'react';
import './infodiv.css';
import 'react-responsive-modal/styles.css';
import 'react-quill/dist/quill.snow.css';
import Modal from 'react-responsive-modal';
import { Close} from '@mui/icons-material';
import ReactQuill from 'react-quill';

function Infodiv() {

    const[isModalOpen, setisModalOpen] = useState(false);
    const close=(<Close />)

  return (
    <div className='infodiv'>
    <div className="content">
        <h2>Wanna share problems faced by you</h2>
    </div>
    <div className="btn">
        <Button onClick={()=>setisModalOpen(true)} style={{color:"white"}}>Raise problems</Button>
    </div>
    <Modal
        open = {isModalOpen}
        closeIcon = {close}
        onClose = {()=> setisModalOpen(false)}
        closeOnEsc
        centercloseOnOverlayClick={false}
        styles={{
          
          overlay: {
            height:"auto",

          },
          height:"400px",
        }}
        >
          <div className="post_content">

         
            <div className="blogpost">
                <ReactQuill /*  value={post}
                onChange={handleQuill} */placeholder="Share your problem..." style={{height:"300px", paddingBottom:"50px"}}/>
            </div>
           
            </div>
            <div className="modal_btn">
            <button className='cancel' onClick={()=>setisModalOpen(false)}>
              Cancel
            </button>
            <button /* onClick={handleSubmit} */type='submit' className='add'>
              Submit
            </button>
            </div>
        </Modal>
</div>
  )
}

export default Infodiv