import { Avatar } from '@mui/material';
import React from 'react';
import './blogpost.css';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';

function Blogpost() {
  return (
    
    <div className='blog_post'>
        <div className="blog_head">
            <div className="user">
                <Avatar />
                <h5>username</h5>
            </div>
            <div className="timestamp">
                <small>timestamp</small>
            </div>
        </div>
        <div className="blog_body">
        <div className="image">
            <img src="https://media.architecturaldigest.com/photos/57c7003fdc03716f7c8289dd/master/pass/IMG%20Worlds%20of%20Adventure%20-%201.jpg" alt="" />
        </div>
        <div className="post_content">
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquam corrupti impedit nobis tempora excepturi, mollitia voluptas, veniam autem tempore, labore nostrum incidunt dolor? Maiores molestias dolore cumque dolorum nam fugit maxime repellendus voluptates nesciunt.</p>
        </div>
        </div>

        <div className="blog_footer">
            <div className="like">
                <FavoriteBorderIcon />
            </div>

            <div className="comment">
                <ChatBubbleOutlineIcon />
            </div>
        </div>
    </div>
  )
}

export default Blogpost